und-color: white;\n\tcolor: black;\n\tmargin: auto;\n  color: #555;\n  font-family: -apple-system, \"Helvetica Neue\", Arial, \"PingFang SC\", \"lucida grande\", \"lucida sans unicode\", lucida, helvetica, \"Hiragino Sans GB\", \"Microsoft YaHei\", \"WenQuanYi Micro Hei\", sans-serif;\n  > h1,\n  > h2,\n  > p{\n  \twidth: 43.75rem;\n  \ttext-align: center;\n  }\n\t> h1{\n\t\tword-break: break-all;\n    font-size: 2rem;\n    font-weight: bold;\n    line-height: 1.5;\n    margin: 10px auto;\n    text-rendering: optimizelegibility;\n    color: inherit;\n    font-family: inherit;\n\t}\n\t> h2{\n\t\tfont-size: 1.5rem;\n\t\tmargin: 0 auto;\n\t\tmargin-top: 2rem;\n    color: inherit;\n    line-height: 1.8;\n    text-rendering: optimizelegibility;\n    font-weight: bold;\n    color: inherit;\n    font-family: inherit;\n\t}\n\t> p{\n\t\ttext-align: justify;\n    text-justify: inter-ideograph;\n    word-break: initial;\n    word-break: break-word;\n    margin: 0 auto 1.5625rem auto;\n    color: #2f2f2f;\n    font-size: 1rem;\n    font-weight: normal;\n    line-height: 1.7;\n    color: #2f2f2f;\n    font-family: inherit;\n\t}\n\t> blockquote{\n\t\tpadding: .625rem .9375rem;\n    margin-bottom: 1.25rem;\n    background-color: whitesmoke;\n    border-left: 4px solid #999999;\n    word-break: break-word;\n    font-size: .9375rem;\n    font-weight: normal;\n    line-height: 1.875rem;\n\t}\n\n\n\t@include susy-media(max-width 555px){\n\t  > h1,\n\t  > h2,\n\t  > p{\n\t  \twidth: 80vw;\n\t  }\n\t}\n\n}\n\n@mixin content(){\n\t> h1{\n\t\tfont-size: 2.5rem;\n\t\tmargin: 0;\n\t\tmargin-bottom: 1rem;\n\t}\n\t> p{\n\t\tfont-size: 1.5rem;\n\t\topacity: .5;\n\t\tmargin: .5rem 0;\n\t}\n\t@include susy-media(max-width 960px){\n\t\t> h1{\n\t\t\tfont-size: 2.5rem !important;\n\t\t}\n\t\t> p{\n\t\t\tfont-size:\t1.25rem !important;\n\t\t}\n\t}\n\n\t@include susy-media(max-width 555px){\n\t\t> h1{\n\t\t\tfont-size: 1.7rem !important;\n\t\t}\n\t\t> p{\n\t\t\tfont-size: 0.857rem !important;\n\t\t}\n\t}\n}\n\n.pc{\n\t@include susy-media(max-width 960px){\n\t\tdisplay: none;\n\t}\n}\n\n@mixin whiteContent(){\n\tbackground-color: white;\n\tcolor: black;\t\n\t@include content();\n}\n\n@mixin blackContent(){\n\tbackground-color: black;\n\tcolor: white;\n\t@include content();\n}\n\n@mixin common(){\n\t@include transform(scale(.7));\n\tmargin-top: -7rem !important;\n\tmargin-bottom: -5rem !important;\n\n\t@include susy-media(max-width 555px){\n\t\t@include transform(scale(.9));\n\t\tmargin-top: 0 !important;\n\t\tmargin-bottom: 0 !important;\n\t}\n}","@import 'src/styles/theme';\n@mixin verticalMiddle($parent) {\n  @if $parent == true {\n    position: relative;\n  } @else {\n    position: absolute;\n    top: 50%;\n    right: -1px; // mysterious thing...\n    transform: translateY(-50%);\n  }\n}\n\n@mixin verticalByTableDisplay($parent) {\n  @if $parent == true {\n    display: table;\n  } @else {\n    display: table-cell;\n    vertical-align: middle;\n  }\n}\n@mixin placeholder() {\n  color: #c2c2ce;\n  opacity: .8;\n}\n\n@mixin underConstruction() {\n  background: repeating-linear-gradient(\n    45deg,\n    #ddd,\n    #ddd 10px,\n    #666 10px,\n    #666 20px\n  );\n  & * {\n    color: #fff;\n    text-shadow: 1px 1px 2px #000;\n  }\n}\n\n@mixin marginCenter {\n  margin-left: auto;\n  margin-right: auto;\n}\n@mixin absoluteCenter($parent, $h, $v) {\n  @if $parent {\n    position: relative;\n  } @else {\n    position: absolute;\n    @if $h { left: 50% }\n    @if $v { top: 50% }\n    @if $h and $v {\n      transform: translate(-50%, -50%);\n    } @else if $h {\n      transform: translateX(-50%);\n    } @else if $v {\n      transform: translateY(-50%);\n    }\n  }\n}\n\n@mixin clearfix {\n  &:after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n}\n\n@mixin docBlock($wide) {\n  @if $wide {\n    width: 75rem;\n    max-width: 100vw;\n    $top: $appbar-height + 4.375rem;\n    margin: $top auto 10rem;\n    padding: 2.375rem 3.125rem 5.25rem;\n  }\n}\n\n@mixin mobileDocBlock {\n  width: 100%;\n  margin: $appbar-height auto 0;\n  padding: 1.125rem $mobile-edge 5rem;\n}\n\n@mixin animation($animate...) {\n    $max: length($animate);\n    $animations: '';\n\n    @for $i from 1 through $max {\n        $animations: #{$animations + nth($animate, $i)};\n\n        @if $i < $max {\n            $animations: #{$animations + \", \"};\n        }\n    }\n    -webkit-animation: $animations;\n    -moz-animation:    $animations;\n    -o-animation:      $animations;\n    animation:         $animations;\n}\n\n@mixin keyframes($animationName) {\n    @-webkit-keyframes #{$animationName} {\n        @content;\n    }\n    @-moz-keyframes #{$animationName} {\n        @content;\n    }\n    @-o-keyframes #{$animationName} {\n        @content;\n    }\n    @keyframes #{$animationName} {\n        @content;\n    }\n}\n\n@mixin transition($t) {\n  -webkit-transition: $t;\n  -moz-transition: $t;\n  -o-transition: $t;\n  transition: $t;\n}\n\n@mixin transform($t) {\n  -webkit-transform: $t;\n  -moz-transform: $t;\n  -o-transform: $t;\n  transform: $t;\n}\n\n@mixin transitions($transitions...) {\n    $max: length($transitions);\n    $ts: '';\n\n    @for $i from 1 through $max {\n        $ts: #{$ts + nth($transitions, $i)};\n\n        @if $i < $max {\n            $ts: #{$ts + \", \"};\n        }\n    }\n    @include transition($ts);\n}\n","@import 'src/styles/susy';\n@import 'src/styles/mixins';\n@import './style';\n\n.hero{\n  @include common();\n  height: 100vh;\n  background-color: #181818;\n  margin: auto;\n  > h1{\n    font-size: 12.5rem;\n    font-weight: bold;\n    color: #1e1e1e;\n    margin: 0 12.5rem;\n    padding-top: 6.25rem;\n    text-shadow: 0 10px 14px;\n  }\n  > div{\n    background-image: url(https://global.speedx.com/speedx/app/appinhand-old.png);\n    width: 100vw;\n    height: 100vh;\n    padding: 0 12.5rem;\n    padding-top: 20.625rem;\n    position: absolute;\n    left: 0;\n    top: 0;\n    background-size: auto 80%;\n    backgrou