imations: '';\n\n    @for $i from 1 through $max {\n        $animations: #{$animations + nth($animate, $i)};\n\n        @if $i < $max {\n            $animations: #{$animations + \", \"};\n        }\n    }\n    -webkit-animation: $animations;\n    -moz-animation:    $animations;\n    -o-animation:      $animations;\n    animation:         $animations;\n}\n\n@mixin keyframes($animationName) {\n    @-webkit-keyframes #{$animationName} {\n        @content;\n    }\n    @-moz-keyframes #{$animationName} {\n        @content;\n    }\n    @-o-keyframes #{$animationName} {\n        @content;\n    }\n    @keyframes #{$animationName} {\n        @content;\n    }\n}\n\n@mixin transition($t) {\n  -webkit-transition: $t;\n  -moz-transition: $t;\n  -o-transition: $t;\n  transition: $t;\n}\n\n@mixin transform($t) {\n  -webkit-transform: $t;\n  -moz-transform: $t;\n  -o-transform: $t;\n  transform: $t;\n}\n\n@mixin transitions($transitions...) {\n    $max: length($transitions);\n    $ts: '';\n\n    @for $i from 1 through $max {\n        $ts: #{$ts + nth($transitions, $i)};\n\n        @if $i < $max {\n            $ts: #{$ts + \", \"};\n        }\n    }\n    @include transition($ts);\n}\n","@import 'src/styles/theme';\n@import 'src/styles/susy';\n@import 'src/styles/mixins';\n@import './style';\n\n.connect{\n  @include common();\n\t@include whiteContent();\n\twidth: 100vw;\n\theight: 50rem;\n\ttext-align: center;\n\tpadding-top: 5rem;\n\toverflow-y: hidden;\n\tposition: relative;\n\t> div{\n\t\t$picWid: 25rem;\n\t\twidth: $picWid;\n\t\tpadding-top: 8.375rem;\n\t\tmargin: auto;\n\t\t> div{\n\t\t\twidth: $picWid;\n\t\t\tposition: absolute;\n\t\t\t@include transition(all 2s ease-in-out)\n\t\t\t&:first-child{\n\t\t\t\tz-index: 100;\n\t\t\t\t> img{\n\t\t\t\t\tmargin-top: 1.6rem;\n\t\t\t\t}\n\t\t\t\t> p{\n\t\t\t\t\twidth: $picWid;\n\t\t\t\t\tdisplay: block;\n\t\t\t\t\tposition: absolute;\n\t\t\t\t\tcolor: white;\n\t\t\t\t\tmargin: 0;\n\t\t\t\t\t&:nth-child(2){\n\t\t\t\t\t\tfont-size: 3rem;\n\