ng, $box);\n  }\n}\n","// rem Support\n// ===========\n\n// rem\n// ---\n// Check for an existing support mixin, or output directly.\n// - $prop  : <css property>\n// - $val   : <css value>\n@mixin susy-rem(\n  $prop,\n  $val\n) {\n  $_reqs: (\n    variable: rhythm-unit rem-with-px-fallback,\n    mixin: rem,\n  );\n  @if susy-support(rem, $_reqs, $warn: false) and $rhythm-unit == rem {\n    @include rem($prop, $val);\n  } @else {\n    #{$prop}: $val;\n  }\n}\n","@import 'src/styles/theme';\n@import 'src/styles/susy';\n@import 'src/styles/mixins';\n\n.box{\n\tpadding-top: 5rem;\n\tbackground-color: white;\n\tcolor: black;\n\tmargin: auto;\n  color: #555;\n  font-family: -apple-system, \"Helvetica Neue\", Arial, \"PingFang SC\", \"lucida grande\", \"lucida sans unicode\", lucida, helvetica, \"Hiragino Sans GB\", \"Microsoft YaHei\", \"WenQuanYi Micro Hei\", sans-serif;\n  > h1,\n  > h2,\n  > p{\n  \twidth: 43.75rem;\n  \ttext-align: center;\n  }\n\t> h1{\n\t\tword-break: break-all;\n    font-size: 2rem;\n    font-weight: bold;\n    line-height: 1.5;\n    margin: 10px auto;\n    text-rendering: optimizelegibility;\n    color: inherit;\n    font-family: inherit;\n\t}\n\t> h2{\n\t\tfont-size: 1.5rem;\n\t\tmargin: 0 auto;\n\t\tmargin-top: 2rem;\n    color: inherit;\n    line-height: 1.8;\n    text-rendering: optimizelegibility;\n    font-weight: bold;\n    color: inherit;\n    font-family: inherit;\n\t}\n\t> p{\n\t\ttext-align: justify;\n    text-justify: inter-ideograph;\n    word-break: initial;\n    word-break: break-word;\n    margin: 0 auto 1.5625rem auto;\n    color: #2f2f2f;\n    font-size: 1rem;\n    font-weight: normal;\n    line-height: 1.7;\n    color: #2f2f2f;\n    font-family: inherit;\n\t}\n\t> blockquote{\n\t\tpadding: .625rem .9375rem;\n    margin-bottom: 1.25rem;\n    background-color: whitesmoke;\n    border-left: 4px solid #999999;\n    word-break: break-word;\n    font-size: .9375rem;\n    font-weight: normal;\n    line-height: 1.875rem;\n\t}\n\n\n\t@i